.block-carroussel{
    height: 300px;
    width: 100%;
}

.carrousel-image{
    width: 100%;
    display: flex;
    align-items: center;
}
.fleche {
    position: absolute;
    width: 30px;
}
.fleche:hover {
    cursor: pointer;
    filter: drop-shadow(0px 0px 5px black);
}
.fleche-gauche {
    transform: rotate(90deg);
    padding: 10px;
}
.fleche-droite {
    transform: rotate(270deg);
    padding: 10px;
    right: 5%;
}

.carrousel-photo {
    width: 100%;
    border-radius: 15px;
    height: 415px;
    object-fit: cover;
    display: none;
}
.carrousel-photo.actif {
    display: flex;
    width: 100%;
}

@media screen and (max-width: 720px) {
    .carrousel-photo {
        height: 250px;
    }
}